input {
	margin-bottom: 0 !important;
}

legend {
	font-weight: 600;
}

.label {
	background: none;
	color: inherit;
	padding: 0;
	font-size: inherit;
}

.form-item label {
	@extend .form-item__label;
}

.form-item {
	max-width: none;
}

.form-fieldset {
	border: 0;
	margin: 2em 0 1em;
	padding: 1em 1.5em;
	background-color: $near-white;

	legend {
		text-transform: uppercase;
	}

	.button {
		margin-top: rem-calc(20);
		margin-bottom: rem-calc(5);
		margin-right: rem-calc(10);

		&:last-child {
			margin-right: 0;
		}
	}
}

//reusable classes
.form-options-list {
	line-height: 160%;

	li {
		margin-bottom: $space-y / 4;

		label {
			font-weight: 400;
		}
	}

	input {
		margin: 0 4px 0 1px;
	}
}

// If in PL
.sg-main {
	.form-fieldset {
		max-width: 600px;
	}
}
